import React, { useState } from 'react';
import { db } from '../firebase';
import { doc, setDoc } from 'firebase/firestore';

const SalesSelf = () => {
    const [allValues, setAllValues] = useState({
        employeeName: '',
        employeeEmail: '',
        managerName: '',
        managerEmail: '',
        role: '',
        jobTitle: '',
    });

    const changeHandler = e => {
        setAllValues({...allValues, [e.target.name]: e.target.value})
    }

    const sendDataToFirestore = async() => {
        try {
            const docRef = await setDoc(doc(db, "users", allValues.employeeName), {
                Employee: allValues.employeeName,
                EmployeeEmail: allValues.employeeEmail
            });
            console.log("Document written with ID: " + docRef);
        } catch (err) {
            console.error('Error sending data to Firestore:', err);
        }
    };

    return (
        <div>
            <input type="text" name='employeeName' id='employeeName' onChange={changeHandler} placeholder='Enter your name'/>
            <input type="text" name='employeeEmail' id='employeeEmail' onChange={changeHandler} placeholder='Enter your email'/>
            <button onClick={sendDataToFirestore}>Send to Firestore</button>
        </div>
    );
};

export default SalesSelf;