import React, { useState } from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import reportWebVitals from './reportWebVitals';

// routes
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import Root from "./routes/root";
import ErrorPage from "./error-page";
import MyInfo from "./routes/myinfo";
import Sales from "./routes/sales";
import SalesSelf from "./routes/sales-self";
import SalesManager from "./routes/sales-manager";
import SalesResult from "./routes/sales-result";
import Promo from "./routes/promo";
import PromoSelf from "./routes/promo-self";
import PromoManager from "./routes/promo-manager";
import PromoResult from "./routes/promo-result";
import Login from "./routes/login";
import Register from "./routes/register";

// styles
import './App.css';

// const [email, setEmail] = useState('');
// const [password, setPassword] = useState('');

// const handleAction = () => {

// }

const router = createBrowserRouter([
  {
    element: <Root />,
    errorElement: <ErrorPage />,
    children: [
      {
        path: "/",
        element: <App />,
      },
      {
        path: "myinfo",
        element: <MyInfo />,
      },
      {
        path: "sales",
        element: <Sales />,
      },
      {
        path: "sales-self",
        element: <SalesSelf />,
      },
      {
        path: "sales-manager",
        element: <SalesManager />,
      },
      {
        path: "promo",
        element: <Promo />,
      },
      {
        path: "promo-self",
        element: <PromoSelf />,
      },
      {
        path: "promo-manager",
        element: <PromoManager />,
      },
      {
        path: "sales-result",
        element: <SalesResult />,
      },
      {
        path: "promo-result",
        element: <PromoResult />,
      },
      {
        path: "login",
        element: <Login />,
      },
      {
        path: "register",
        element: <Register />,
      },
    ],
  },
]);

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
