import React, { useState } from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import { db, auth } from '../firebase';
import { getAuth, createUserWithEmailAndPassword, sendEmailVerification, onAuthStateChanged } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';

const Register = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    let navigate = useNavigate();

    const userRegister = () => {
        const authentication = getAuth();
        const user = authentication.currentUser;
        createUserWithEmailAndPassword(authentication,  email, password)
        .then((res) => {
            const displayName = user.displayName;
            const email = user.email;
            const photoURL = user.photoURL;
            const emailVerified = user.emailVerified;
            const uid = user.uid;
            // console.log(userCredential);
            console.log(displayName, email, photoURL, emailVerified, uid);

            // 네트워크에서 막힘...
            // sendEmailVerification(authentication.currentUser)
            // .then(() => {
            //     // Email verification sent!
            //     console.log("Email verification sent!");
            //     navigate('/');
            // })
            // .catch((err) => {
            //     console.error(err);
            // })
            navigate('/');
        })
        .catch((err) => {
            console.log(err)
        });

    };

    const userInfo = () => {
        console.log(auth.currentUser);
    };

    return (
        <div>
            <div className="register-form">
                <h3>
                    Register Form
                </h3>
            </div>

            <Box
                component="form"
                sx={{
                    '& > :not(style)': { m: 1, width: '25ch' },
                }}
                noValidate
                autoComplete="off"
            >
                <TextField id="email" label="Enter the Email" variant="outlined" onChange={(e) => setEmail(e.target.value)}/>
                <TextField id="password" type="password" label="Enter the Password" variant="outlined" onChange={(e) => setPassword(e.target.value)}/>
            </Box>
            <Button variant='contained' onClick={userRegister}>Register</Button>
            <Button variant='contained' onClick={userInfo}>UserInfo</Button>
        </div>
    )
}

export default Register;