import React, { useState, useEffect } from 'react';
import Papa from 'papaparse';

const MyInfo = () => {
    const [csvData, setCsvData] = useState([]);

    useEffect(() => {
        // Replace 'your-file.csv' with the path or URL to your CSV file
        const csvFilePath = './data/sample_org.csv';

        // Fetch the CSV file
        fetch(csvFilePath)
        .then((res) => res.text())
        .then((csvText) => {
            // Parse the CSV text
            Papa.parse(csvText, {
                complete: (res) => {
                    // 'res.data' contains the parsed CSV data
                    setCsvData(res.data);
                    console.log(res.data);
                },
                header: true, // Set to true if your CSV file has a header row
            });
        })
        .catch((error) => {
            console.error('Error fetching or parsing CSV file:', error);
        });
    }, []); // The empty dependency array ensures that this effect runs only once, similar to componentDidMount

    return (
        <div>
            <h2>Parsed CSV Data:</h2>
            <ul>
                {csvData.map((row, index) => (
                    <li key={index}>{JSON.stringify(row)}</li>
                ))}
            </ul>
        </div>
    );
};

export default MyInfo;